import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import { makeStyles } from '@material-ui/core/styles';
import '../global.css';

const educationStyles = makeStyles({
  root: { marginTop: `10px` },
  cardTitle: {
    color: `#375c85`,
    fontSize: `1.38316rem`,
    fontWeight: 800,
    marginBottom: `10px`,
  },
  school: {
    fontSize: `17px`,
    lineHeight: `1.3`,
    color: `rgb(17, 17, 17)`,
    fontWeight: `600`,
  },
  date: { opacity: `.9` },
  details: {
    fontSize: `16px`,
    fontFamily: `-apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto,
    'Helvetica Neue', Arial, sans-serif`,
    marginTop: `15px`,
    lineHeight: `1.5`,
  },
});

const Education = () => {
  const classes = educationStyles();
  return (
    <Card className={classes.root}>
      <CardContent>
        <div className={classes.cardTitle}>
          <span role={'img'} aria-label={'Cap emoji'}>
            üéì
          </span>{' '}
          Education
        </div>
        <div className={classes.school}>Louisiana State University</div>
        <div className={classes.date}>May 2020</div>
        <div className={classes.details}>
          Computer Science, Bachelor of Science <br />
          Information Technology Management (ITM) Minor
        </div>
      </CardContent>
    </Card>
  );
};

export default Education;
