import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Divider from '@material-ui/core/Divider';
import { makeStyles } from '@material-ui/core/styles';
import '../global.css';

const experienceStyles = makeStyles({
  cardTitle: {
    color: `#375c85`,
    fontSize: `1.38316rem`,
    fontWeight: 800,
    marginBottom: `10px`,
  },
  company: {
    fontSize: `16px`,
    lineHeight: `1.3`,
    opacity: `.9`,
  },
  onSite: {
    color: `#00a152`,
    opacity: `.9`,
  },
  remote: {
    color: `#3f51b5`,
    opacity: `.9`,
  },
  position: {
    color: `rgb(17, 17, 17)`,
    fontWeight: `600`,
  },
  details: {
    fontSize: `16px`,
    fontFamily: `-apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto,
    'Helvetica Neue', Arial, sans-serif`,
    marginTop: `15px`,
    lineHeight: `1.5`,
  },
});

const Experience = () => {
  const classes = experienceStyles();
  return (
    <Card>
      <CardContent>
        <div className={classes.cardTitle}>
          <span role={'img'} aria-label={'Computer emoji'}>
            üñ•Ô∏è
          </span>{' '}
          Experience
        </div>
        <div className={classes.position}>
          Software Developer | <span className={classes.remote}>Remote</span>
        </div>
        <div className={classes.company}>
          Applied Research Associates, Inc. <br />
          June 2017 - present
        </div>
        <div className={classes.details}>
          Primarily develop progressive web apps and Android apps
          <br />
          Developed native mobile applications with Android Studio and Xamarin
          Forms
          <br />
          Considerable experience with Angular JS, JavaScript/Typescript,
          HTML5/CSS
          <br />
          Experience with backend tools such as SQL Server, Identity Server,
          LLBLGen
          <br />
          Scrum methods, including daily stand-ups, product backlogs, and effort
          estimation
        </div>
        <Divider style={{ margin: `10px 0` }} />
        <div className={classes.position}>
          Website &amp; Front-End Developer |{' '}
          <span className={classes.onSite}>On-Site</span>
        </div>
        <div className={classes.company}>
          JAB Software, LLC. <br />
          May 2014 - May 2016
        </div>
        <div className={classes.details}>
          Developed company{' '}
          <a
            href={`https://jabcode.com/`}
            target={`_blank`}
            style={{
              textDecoration: `none`,
              borderBottom: `2px dotted #00a152`,
              color: `black`,
            }}
          >
            website
          </a>{' '}
          using WordPress, HTML5, SASS <br />
          Developed SPAs using Angular, Material Design <br />
          Tested server-side and client-side software for web applications
          <br />
          Created bug reports and user stories using Microsoft‚Äôs TFS and
          PivotalTracker
        </div>
      </CardContent>
    </Card>
  );
};

export default Experience;
